@import url('https://fonts.googleapis.com/css?family=Special+Elite|Work+Sans&display=swap');
@import 'variables';
@import 'mixin';



.header {

  background: {
    image: url('../../image/health-vegetables_1280.jpg');
  }
  background: {
    repeat: no-repeat;
  }
  background-size: cover;
  background: {
    position: left top;
  }
  margin: {
    top: 1px;
  }
  max: {
    height: 100vh;
  }
  display: flex;
  @include size;
  animation: anim 10s ease 1s infinite alternate none;

  @keyframes anim {
    0% {
      background-position: left top;
      background-size: 100%;  
    }

    100% {
      background-position: 0 13%;
      background-size: 110%;   
    }
  }

  & .logo {
    width: 30vw;

    &__text {
      color: white;

      font: {
        size: 4rem;
      }

      margin: {
        left: 15%;
      }

      @media (max-width: 769px) {
        font: {
          size: 2rem;
        }


        margin: 0 5% 5% 5%;
      }

      @media (min-width: 1439px) {
        font: {
          size: 6rem;
        }
      }

    }

    @media (max-width: 769px) {
      width: 100vw;
      height: 20%;

      margin: {
        bottom: 15px;
      }
    }
  }

  & .navigation {
    width: 55vw;

    margin: {
      top: 30px;
    }

    & .list {
      list: {
        style: none;
      }

      display: flex;

      flex: {
        direction: column;
      }

      align: {
        content: space-between;
      }

      height: 90%;
      padding: 0 1% 0 1%;

      &__element {
        height: 25%;

        text: {
          align: center;
        }

        .list__a {
          text: {
            decoration: none;
          }

          padding: 25px 50px 25px 50px;
          color: $blue;

          font: {
            family: 'Special Elite', cursive;
            weight: 800;
            size: 2rem;
          }

          @media (max-width: 769px) {
            font: {
              size: 1rem;
            }

            padding: 15px 35px 15px 35px;
          }

          @media (min-width: 1439px) {
            font: {
              size: 3rem;
            }

          }
        }

        &:nth-child(1) .list__a {
          background: {
            color: $red;
            // color: #161C40;
          }

          margin: {
            top: 5%;
          }

          color: $yellow;

        }

        &:nth-child(1) {
          transform: rotate(5deg);
          transition: .5s;

          &:hover {
            transform: rotate(1deg);
            transform: scale(1.2, 1.2)
          }
        }

        &:nth-child(2) .list__a {
          background: {
            color: $yellow;

          }

          color: $blueDark;
        }

        &:nth-child(2) {
          transform: rotate(-2deg);
          transition: .5s;

          &:hover {
            transform: rotate(1deg);
            transform: scale(1.2, 1.2)
          }
        }

        &:nth-child(3) .list__a {
          background: {
            color: $red;
            // color: #161C40;
          }

          color: $yellow;
        }

        &:nth-child(3) {
          transform: rotate(10deg);
          transition: .5s;

          &:hover {
            transform: rotate(3deg);
            transform: scale(1.2, 1.2)
          }
        }

        &:nth-child(4) .list__a {
          background-color: $blueDark;

        }

        &:nth-child(4) {
          transform: rotate(1deg);
          transition: .5s;

          &:hover {
            transform: rotate(3deg);
            transform: scale(1.2, 1.2)
          }
        }
      }

      @media (max-width: 769px) {
        margin-top: 40px;
        height: 80%;
      }
    }

    @media (max-width: 769px) {
      width: 100vw;
      height: 400px;
    }
  }

  .element {
    width: 15vw;

    & .icon {
      width: 50px;
      height: 50px;
      color: white;
      transition: .7s;

      &:hover {
        transform: rotate(360deg);
      }

    }

    @media (max-width: 769px) {
      display: none;

    }

  }

  @media (max-width: 769px) {
    flex-direction: column;
    overflow-x: hidden;
    height: 80%;
    background-image: url('../../image/health-vegetables_640.jpg');
    background-repeat: no-repeat;
    background-size: cover;

    margin: {
      top: 10px;
      bottom: 10px;
    }
  }

  @media (max-width: 1441px) {
    @keyframes anim {

      0% {

        background-position: left top;
      }

      100% {
        background-position: 57% 0;
      }
    }
  }

  @media (orientation: landscape) {
    height: 130%;
  }
}